
export default {
  name: "ScrapPage",
  data() {
    return {
      url: "",
      loading: false,
    };
  },
  watch: {
    url(val) {
      if (val.length > 0) this.extract();
    },
  },
  methods: {
    async extract() {
      try {
        this.loading = true;

        const { data } = await this.$scrapApi.addDoctorFromSitemap({
          url: this.url,
        });

        console.log(data);

        // this.url = "";
      } catch (error) {
        console.log(error.message);
      } finally {
        this.loading = false;
      }
    },
    async fetchSitemap() {
      try {
        this.loading = true;
        const { data: xmlData } = await this.$scrapApi.scrapSitemap({
          url: this.url,
        });

        const parser = new DOMParser();
        const xmlDoc = parser.parseFromString(xmlData, "text/xml");
        const urls = Array.from(xmlDoc.getElementsByTagName("loc")).map(
          (node) => node.textContent
        );

        const uniqueUrls = Array.from(new Set(urls)).map((url) => ({
          url,
          ref: "doctorbangladesh_pages",
        }));

        await this.$scrapApi.addSitemap({ urls: uniqueUrls });

        // this.url = "";
      } catch (error) {
        console.log(error.message);
      } finally {
        this.loading = false;
      }
    },
    async pasteFromClipboard() {
      try {
        const text = await navigator.clipboard.readText();
        this.url = text;
      } catch (error) {
        console.error("Failed to read clipboard contents:", error);
      }
    },
  },
};
